/* eslint-disable */
import crypto from 'crypto';

import icon from '@/components/icon/ICON.vue';
 
export default {
    name: "Header",
    props: {
        headerTop: Boolean,
        headerHidden: Boolean
    },
    components: {
        icon
    },
    data: () => ({
        search: '',
        mask: {
            status: false,
            zIndex: 996
        },
        menu: {
            dom: false,
            main: false
        },
        searchResult: false,
        logoData: '',
        clientHeight: 0,
        historyList: []
    }),
    computed: {
        siteName: function () {
            return this.$store.state.global.hexoConfig.site.title;
        },
        navItems: function () {
            let menu = this.$store.state.global.themeConfig.menu,
                icons = this.$store.state.global.themeConfig.menu_icons,
                groups = this.$store.state.global.themeConfig.menu_groups;
                
            let menuData = [];
            for (let i in groups) {
                if (i !== 'enable') {
                    menuData[this.btoa(groups[i])] = [];
                }
            }
            for (let j in menu) {
                menuData[this.btoa(groups[j])].push({
                    name: j,
                    path: menu[j],
                    icon: icons[j],
                })
            }
            return Object.assign({}, menuData);
            
        },
        siteUrl: function () {
            return this.$store.state.global.hexoConfig.url.url;
        },
        posts: function () {
            return this.$store.state.archives.posts.data;
        },
        displayPosts: function () {
            const search = this.search;
            return search.length ? this.posts.filter(post => post.title.toLowerCase().indexOf(search.toLowerCase()) !== -1) : [];
        }
    },
    created: async function () {
        await this.$store.dispatch('archives/fetchArchivesPosts');
        await this.loadLogo();
    },
    mounted: async function () {
        let that = this;
        that.clientHeight = `${document.documentElement.clientHeight}`;
        window.onresize = () => {
            that.clientHeight = `${document.documentElement.clientHeight}`;
        };

        let historyListJson = localStorage.getItem('historyList') || '[]';
        this.historyList = JSON.parse(historyListJson);
    },
    methods: {
        atob: function (str) {
            return Buffer.from(str, 'base64').toString('utf8');
        },
        btoa: function (str) {
            return Buffer.from(str).toString('base64');
        },
        loadLogo: async function () {
            let siteName = this.$store.state.global.hexoConfig.site.title;
            let siteNameHash = crypto.createHash('md5').update(siteName, 'utf8').digest('hex');
            let logoData = localStorage.getItem(`${siteNameHash}_LOGO`);
            if (!logoData) {
                let logoUrl = this.$store.state.global.themeConfig.logo.url;
                let res = await this.$axios.get(logoUrl);

                if (res.status === 200) {
                    logoData = 'data:image/svg+xml;base64,' + window.btoa(res.data);
                    localStorage.setItem(`${siteNameHash}_LOGO`, logoData);
                }
            }
            this.logoData = logoData;
        },
        checkHighlight: function(part) {
            let highlightSiteTitle = this.$store.state.global.themeConfig.highlight_site_title;

            if (!highlightSiteTitle.enable) return false;

            if (highlightSiteTitle.highlight_part.indexOf(part) !== -1) {
                return true;
            }

            return false;
        },
        inputStatus: function (status) {
            if (status === 'focus') {
                this.mask.zIndex = 996;
                this.mask.status = true;
                this.searchResult = true;
                document.querySelector('body').style.setProperty('overflow', 'hidden');
                document.querySelector('.' + this.$style.header).style.setProperty('background-color', getComputedStyle(document.querySelector('.' + this.$style.header), ':hover').backgroundColor);
            } else if (this.search == '' && status === 'blur') {
                this.mask.status = false;
                this.mask.zIndex = null;
                this.searchResult = false;
                document.querySelector('.' + this.$style.header).style.setProperty('background-color', '');
                document.querySelector('body').style.setProperty('overflow', '');
            } else {
                this.insertArray(this.historyList, this.search, (item) => { return item === this.search }, 5) 
                localStorage.setItem('historyList', JSON.stringify(this.historyList));
            }
        },
        toggleSearchStatus: function (status) {
            if (status == true) {
                this.mask.zIndex = 996;
                this.mask.status = true;
                this.searchResult = true;
                document.querySelector('body').style.setProperty('overflow', 'hidden');
                document.querySelector('.' + this.$style.header).style.setProperty('background-color', getComputedStyle(document.querySelector('.' + this.$style.header), ':hover').backgroundColor);
            } else {
                this.mask.status = false;
                this.mask.zIndex = null;
                this.searchResult = false;
                document.querySelector('.' + this.$style.header).style.setProperty('background-color', '');
                document.querySelector('body').style.setProperty('overflow', '');
            }
        },
        toggleMenuStatus: function (status) {
            let that = this;
            if (status == true) {
                document.querySelector('body').style.setProperty('overflow', 'hidden');
                that.menu.dom = true;
                that.mask.zIndex = 1002;
                that.mask.status = true;
                setTimeout(() => {
                    that.menu.main = true 
                }, 0);
            } else {
                that.menu.main = false;
                that.mask.status = false;
                that.mask.zIndex = null;
                setTimeout(() => {
                    that.menu.dom = false;
                    document.querySelector('body').style.setProperty('overflow', '');
                }, 200);
            }
        },
        cleanHistory: function () {
            localStorage.removeItem('historyList')
        },
        insertArray: function (arr, val, compare, maxlen = 15) {
            const index = arr.findIndex(compare)
            if (index === 0) {
                return;
            }
            if (index > 0) {
                arr.splice(index, 1);
            }
            arr.unshift(val);
            if (maxlen && arr.length > maxlen) {
                arr.pop();
            }
        } 
    }
}