<template>
  <div id="app">
        <Loading />

        <Header :headerTop="headerTop" :headerHidden="headerHidden" />

        <main>
            <Background :scrolled="scrolled" :bg="bg" :bgHeight="bgHeight" />
            
            <router-view :scrolled="scrolled" :bgHeight="bgHeight" />
        </main>

        <Footer />
    </div>
</template>

<style lang="less">

</style>

<script>
/* eslint-disable */
import Header from "@/components/header/Header.vue";
import Footer from "@/components/footer/Footer.vue";
import Loading from "@/components/loading/Loading.vue";
import Background from "@/components/background/Background.vue";

export default {
    name: "App",
    components: {
        Header,
        Footer,
        Loading,
        Background
    },
    data: () => ({
        scrolled: 0,
        headerTop: true,
        headerHidden: false,
        bgHeight: 512,
    }),
    mounted: function () {
        this.$progress.start();
        this.bgHeight = this.getBgHeight(window.innerHeight, '80', '60');
        this.initScroll();
        this.$progress.done();
    },
    computed: {
        theme: function() {
            return this.$store.state.global.themeConfig;
        },
        bg: function() {
            return this.$store.state.global.themeConfig.background.url;
        }
    },
    watch: {
        'scrolled': function (val) {
            if (val <= 0) {
                this.scrolled = 0
            }
        }
    },
    methods: {
        initScroll: function () {
            const h = this.bgHeight - 280;
            let that = this;

            function getScrollTop() {
                let scrollTop = 0, bodyScrollTop = 0, documentScrollTop = 0;
                if(document.body){
                    bodyScrollTop = document.body.scrollTop;
                }
                if(document.documentElement){
                    documentScrollTop = document.documentElement.scrollTop;
                }
                scrollTop = (bodyScrollTop - documentScrollTop > 0) ? bodyScrollTop : documentScrollTop;
                return scrollTop;
            }           
            function getScrollHeight() {
                let bSH = 0, dSH = 0, scrollHeight = 0;
                if (document.body) {
                    bSH = document.body.scrollHeight;
                }
                if (document.documentElement) {
                    dSH = document.documentElement.scrollHeight;
                }
                scrollHeight = (bSH - dSH > 0) ? bSH : dSH ;
                return scrollHeight;
            }
            function getWindowHeight() {
                let windowHeight = 0;
                if (document.compatMode == "CSS1Compat") {
                    windowHeight = document.documentElement.clientHeight;
                } else {
                    windowHeight = document.body.clientHeight;
                }
                return windowHeight;
            }
            function scrollEvent () {
                let scrolled = 0;
                if (typeof window.pageYOffset != "undefined") {
                    scrolled = window.pageYOffset;
                } else if (typeof document.compatMode != "undefined" && document.compatMode != "BackCompat") {
                    scrolled = document.documentElement.scrollTop;
                } else if (typeof document.body != "undefined") {
                    scrolled = document.body.scrollTop;
                }
                that.scrolled = Math.round(scrolled * 0.3);
                if (scrolled >= h) {
                    that.headerTop = false;
                } else if (scrolled < h) {
                    that.headerTop = true;
                }

                window.addEventListener('scroll', () => {
                    let e = 0;
                    if (typeof window.pageYOffset != "undefined") {
                        e = window.pageYOffset;
                    } else if (typeof document.compatMode != "undefined" && document.compatMode != "BackCompat") {
                        e = document.documentElement.scrollTop;
                    } else if (typeof document.body != "undefined") {
                        e = document.body.scrollTop;
                    }

                    if (e < scrolled) {
                        that.headerHidden = false;
                    }
                    if (scrolled >= h && e > scrolled) {
                        that.headerHidden = true;
                    }
                })
            }
                            
            window.addEventListener('scroll', () => {
                // 
                console.log(getScrollTop())
                if (getScrollTop() > 0) {
                    scrollEvent();
                }

                // 
                if (getScrollTop() + getWindowHeight() !== getScrollHeight()) {
                    scrollEvent();
                }
            })
        },
        getBgHeight: (windowHeight, bannerHeight, mobileBannerHeight) => {
            windowHeight = windowHeight || 560;
            if (windowHeight > window.screen.availHeight) {
                windowHeight = window.screen.availHeight;
            }
            bannerHeight = bannerHeight.trim();
            mobileBannerHeight = mobileBannerHeight.trim();
            if (window.innerHeight > window.innerWidth) {
                bannerHeight = parseFloat(mobileBannerHeight);
            } else {
                bannerHeight = parseFloat(bannerHeight);
            }
            bannerHeight = Math.round(windowHeight * bannerHeight / 100);
            return bannerHeight;
        }
    }
};
</script>