/* eslint-disable */
import Vue from 'vue';
import ProgressVue from './component/Progress.vue';

const ProgressConstructor = Vue.extend(ProgressVue);

export const installProgress = {
    install(Vue, options) {
        const ProgressInstance = new ProgressConstructor();
        ProgressInstance.settings = Object.assign(ProgressInstance.settings, options)
        ProgressInstance.vm = ProgressInstance.$mount();
        document.body.appendChild(ProgressInstance.vm.$el);
        
        const Progress = {
            start: ProgressInstance.start,
            set: ProgressInstance.set,
            done: ProgressInstance.done,
        };


        Vue.prototype.$progress = Progress;
    }
};