import Vue from "vue";
import Router from "vue-router";
import Routes from "./routes";

Vue.use(Router);

const isHistory = typeof (history.pushState) === 'function';

const router = new Router({
  // mode: isHistory ? 'history' : 'hash',
  mode: 'history',
  base: process.env.BASE_URL,
  routes: Routes
});

export default router;
