export default {
    name: "Footer",
    data: () => ({
        hitokoto: ''
    }),
    watch: {
        $route: function (to, from) {
            this.getHitokoto();
        }
    },
    computed: {
        siteName: function () {
            return this.$store.state.global.hexoConfig.site.title;
        }
    },
    async created() {
        this.getHitokoto();
    },
    methods: {
        getHitokoto: function () {
            let that = this;
            this.axios
                .get('https://api.aim.moe/Common/Hitokoto')
                .then(response => that.hitokoto = response.data.data.text);
        }
    }
}