import { fetchAllTags, fetchPostsListByTag } from '@/api';
import { Tag, SpecificPostsList } from '@/models/postsList.class';

const state = {
    tags: [],
    oneTagPosts: new SpecificPostsList()
};

// getters
const getters = {};

// actions
const actions = {
    fetchTags: async ({ commit }) => {
        const { data } = await fetchAllTags();
        commit('setTags', { data });
    },
    fetchRelatedPostsOfTag: async ({ commit }, { slug }) => {
        const { data } = await fetchPostsListByTag(slug);
        commit('setRelatedPostsOfTag', { data });
    }
};

// mutations
const mutations = {
    setTags: (state, { data }) => {
        state.tags = (data).map(one => new Tag(one));
    },
    setRelatedPostsOfTag: (state, { data }) => {
        state.oneTagPosts = new SpecificPostsList(data);
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}


