import { Post } from '@/models/postsList.class';

export class Article extends Post {
    title = '';
    date = '';
    updated = '';
    comments = false;
    path = '';
    covers = null;
    excerpt = null;
    content = '';
    locked = null;
    password = null;

    constructor(raw) {
        super(raw);
        if (raw) {
            for (const key of Object.keys(this)) {
                if (raw.hasOwnProperty(key)) {
                    Object.assign(this, { [key]: raw[key] });
                }
            }
        }
    }
}

