const Home = (resolve) => require(["@/views/home/Home.vue"], resolve);
const Tags = (resolve) => require(["@/views/tags/Tags.vue"], resolve);
const Related = (resolve) => require(["@/views/related/Related.vue"], resolve);
const Page404 = (resolve) => require(["@/views/page404/Page404.vue"], resolve);
const Archive = (resolve) => require(["@/views/archive/Archive.vue"], resolve);
const Archives = (resolve) => require(["@/views/archives/Archives.vue"], resolve);
const Categories = (resolve) => require(["@/views/categories/Categories.vue"], resolve);

export default [
    { path: "/", name: "home", component: Home },
    { path: '/tags', name: 'tags', component: Tags },
    { path: '/404', name: '404', component: Page404 },
    { path: '/archives', name: 'archives', component: Archives },
    { path: '/categories', name: 'categories', component: Categories },
    { path: '/related/:type/:slug', name: 'related', component: Related },
    { path: '/pages/*', name: 'page', component: Archive, props: { isImplicit: true } },
    { path: '/posts/:slug', name: 'post', component: Archive, props: { isImplicit: false } },
    { path: '/*', redirect: { name: '404' } }
];
