/* eslint-disable */
import { fetchPostsList } from '@/api';
import { PostsList } from '@/models/postsList.class';

const state = {
    postsList: null,
    page: 0
};

// getters
const getters = {};

// actions
const actions = {
    fetchHomePostsList: async ({ commit }, { page }) => {
        const { data } = await fetchPostsList(page);
        commit('setHomePostsList', { data, page });
    }
};

// mutations
const mutations = {
    setHomePostsList: (state, { data, page }) => {
        if (page == 1) {
            state.postsList = new PostsList(data);
        } else {
            data.data.forEach(element => {
                state.postsList.data.push(element);
            })
        }
        state.page = page;            
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}


