const typesData = {
    success: 'success',
    info: 'info',
    warning: 'warning',
    error: 'error'
};

export default {
    name: "Notification",
    data: () => ({
        domShow: false,
        visible: true,
        nId: 0,
        msg: '',
        type: '',
    }),
    computed: {
        typeClass() {
            return this.type ? this.$style[typesData[this.type]] : ''
        }
    },
    mounted() {
        // this.setTimer();
    },
    methods: {
        //setTimer: function () {
        //    setTimeout(() => {
        //        this.close();
        //    }, 5000)
        //},
        close: function () {
            this.domShow = false;
            setTimeout(() => {
                this.$destroy(true);
                this.$el.parentNode.removeChild(this.$el);
            }, 500);
        }
    }
};