/* eslint-disable no-console */
console.log(`${'\n'} %c THI Lite For Hexo %c v${process.env.THI_LITE_VERSION} ${process.env.GIT_HASH} ${'\n'}`, "color:rgb(255, 242, 242);background:rgb(244, 164, 164);padding:5px 0;border-radius:4px 0 0 4px;", "color:rgb(244, 164, 164);background:rgb(255, 242, 242);padding:5px 0;border-radius:0 4px 4px 0;");

if (process.env.NODE_ENV === "production") {
    if (process.env.GIT_HASH !== localStorage.getItem('GIT_HASH')) {
        caches.keys().then(function (keyList) {
            return Promise.all(keyList.map(function (key) {
                return caches.delete(key);
            }));
        });
        localStorage.setItem('GIT_HASH', process.env.GIT_HASH);
        console.log('The cache content has been cleared, refresh and rebuild the cache');
    }
}