/* eslint-disable */
import Vue from 'vue';
import Notification from './component/Notification.vue';

const NotificationConstructor = Vue.extend(Notification);

let nId = 0;

let dom = [];

const NotificationDiv = (options) => {
    options.nId = nId;
    const NotificationInstance = new NotificationConstructor({
        data: options
    });
    NotificationInstance.vm = NotificationInstance.$mount();
    NotificationInstance.vm.domShow = true;
    document.body.appendChild(NotificationInstance.vm.$el);
    dom.push(NotificationInstance);
    Vue.nextTick(() => {
        NotificationInstance.timer = setTimeout(() => {
            NotificationInstance.close();
            dom.splice(dom.indexOf(NotificationInstance), 1);
            nId = dom.length;
            setTimeout(() => {
                dom.forEach(element => {
                    element.vm.nId = element.vm.nId - 1;
                });
            }, 500)
        }, 5000);
    });
    nId++;
    return NotificationInstance;
}

export const installNotification = {
    install(Vue, options) {
        Vue.prototype.$notification = NotificationDiv;
    }
};