export class DisqusOptions {
    enable = false;
    shortname = '';

    constructor(raw) {
        if (raw) {
            for (const key of Object.keys(this)) {
                if (raw.hasOwnProperty(key)) {
                    Object.assign(this, { [key]: raw[key] });
                }
            }
        }
    }
}