import { fetchImplicitPageBySource, fetchPostBySlug } from '@/api';
import { Page, Article } from '@/models/article.class';

const state = {
    target: new Page(),
    isImplicit: true
};

// getters
const getters = {};

// actions
const actions = {
    fetchArchiveTarget: async ({ commit }, { isImplicit, sourceOrSlug }) => {
        if (isImplicit) {
            try {
                const { data } = await fetchImplicitPageBySource(sourceOrSlug);
                commit('setArchiveTarget', { target: data, isImplicit: true });
            } catch (err) {
                const { data } = await fetchImplicitPageBySource(sourceOrSlug.replace(/\/?$/, '/index'));
                commit('setArchiveTarget', { target: data, isImplicit: true });
            }

        } else {
            const { data } = await fetchPostBySlug(sourceOrSlug);
            commit('setArchiveTarget', { target: data, isImplicit: false });
        }
    },
    switchArchiveLockedStatus: ({ commit }) => {
        commit('setArchiveLockedStatus');
    }
};

// mutations
const mutations = {
    setArchiveTarget: (state, { target, isImplicit }) => {
        state.target = isImplicit ? new Page(target) : new Article(target);
        state.isImplicit = isImplicit;
    },
    setArchiveLockedStatus: (state) => {
        state.target.locked = !state.target.locked
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}