/* eslint-disable */
import { PostsList, Post } from '@/models/postsList.class';
import { fetchPostsList, fetchAllPosts } from '@/api';
import moment from 'moment';

const state = {
    postsList: new PostsList(),
    page: 0,
    posts: new PostsList()
};

// getters
const getters = {
    timeLineList: (state) => {
        const result = {};
        for (const post of state.postsList.data) {
            const date = moment(post.date);
            const yearAndMonth = `${date.year()}-${date.month() + 1}`;
            if (result[yearAndMonth]) {
                result[yearAndMonth].push(post);
            } else {
                result[yearAndMonth] = [post];
            }
        }
        return { keys: Object.keys(result), entities: result };
    }
};

// actions
const actions = {
    fetchArchivesPostsList: async ({ commit }, { page }) => {
        const { data } = await fetchPostsList(page);
        commit('setArchivesPostsList', { data, page });
    },
    fetchArchivesPosts: async ({ commit }) => {
        const { data } = await fetchAllPosts();
        commit('setArchivesPosts', { data });
    }
};

// mutations
const mutations = {
    setArchivesPosts: (state, { data }) => {
        state.posts = new PostsList(data);
    },
    setArchivesPostsList: (state, { data, page }) => {
        state.postsList = new PostsList(data);
        state.page = page;
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}