export default {
    name: "Background",
    props: {
        scrolled: Number,
        bg: String,
        bgHeight: Number
    },
    watch: {
        bgHeight: function (newVal, oldVal) {
            let s = document.createElement('style');
            s.type = 'text/css';
            this.test = s.textContent = '.' + this.$style.background + '::before{height:' + newVal + 'px!important}';
            document.head.appendChild(s);
        }
    }
};