// actions
const actions = {
    fetchCategories: async ({ commit }) => {
        const { data } = await fetchAllCategories();
        for (let i in data) {
            const postList = (await fetchPostsListByCategory(data[i].slug)).data.postlist;
            data[i].postList = (postList).map(one => new Post(one));
        }
        commit('setCategories', { data });
    },
    fetchRelatedPostsOfCategory: async ({ commit }, { slug }) => {
        const { data } = await fetchPostsListByCategory(slug);
        commit('setRelatedPostsOfCategory', { data });
    }
};

// mutations
const mutations = {
    setCategories: (state, { data }) => {
        state.categories = (data).map(one => new Category(one));
    },
    setRelatedPostsOfCategory: (state, { data }) => {
        state.oneCategoryPosts = new SpecificPostsList(data);
    }
};

