/* eslint-disable */
export default {
    name: "Progress",
    data: () => ({
        status: null,
        percent: 0,
        folding: false,
        settings: {
            minimum: 0.08,
            speed: 200,
            trickle: false,
            trickleSpeed: 200
        }
    }),
    methods: {
        set: function (n) {
            let that = this;

            let queue = (function () {
                var pending = [];

                function next() {
                    var fn = pending.shift();
                    if (fn) {
                        fn(next);
                    }
                }

                return function (fn) {
                    pending.push(fn);
                    if (pending.length == 1) next();
                };
            })();


            let toBarPerc = function (n) {
                return (n) * 100;
            }

            n = this.clamp(n, that.settings.minimum, 1);
            
            that.status = (n === 1 ? null : n);
            that.percent = Math.floor(toBarPerc(that.status || 0));

            var speed = that.settings.speed;

            queue(function (next) {
                if (n >= 0.99) {
                    setTimeout(function () {
                        that.remove();
                        next();
                    }, speed);
                } else {
                    setTimeout(next, speed);
                }
            });

            return that;
        },
        isStarted: function () {
            return typeof this.status === 'number';
        },
        start: function () {
            let that = this;
            if (!that.status) that.set(0);
            that.set(0);
            var work = function () {
                setTimeout(function () {
                    if (!that.status) return;
                    that.trickle();
                    work();
                }, that.settings.trickleSpeed);
            };

            if (that.settings.trickle) work();

            return this;
        },
        done: function (force) {
            if (!force && !this.status) return this;

            return this.inc(0.3 + 0.5 * Math.random()).set(1);
        },
        inc: function (amount) {
            var n = this.status;

            if (!n) {
                return this.start();
            } else if (n > 1) {
                return;
            } else {
                if (typeof amount !== 'number') {
                    if (n >= 0 && n < 0.2) { amount = 0.1; }
                    else if (n >= 0.2 && n < 0.5) { amount = 0.04; }
                    else if (n >= 0.5 && n < 0.8) { amount = 0.02; }
                    else if (n >= 0.8 && n < 0.99) { amount = 0.005; }
                    else { amount = 0; }
                }

                n = this.clamp(n + amount, 0, 1);
                return this.set(n);
            }
        },
        trickle: function () {
            return this.inc();
        },
        remove: function () {
            this.folding = true;
            setTimeout(() => {
                this.$destroy(true);
                this.$el.parentNode.removeChild(this.$el);
            }, 500);
        },
        clamp: function (n, min, max) {
            if (n < min) return min;
            if (n > max) return max;
            return n;
        }
    }
};