import Vue from "vue";

import { fetchHexoConfig } from '@/api';
import { HexoConfig } from '@/models/hexoConfig.class';
import { Theme } from '@/models/themeConfig.class';
import { installGoogleAnalytics } from "@/vue-install/analytics";

const state = {
    hexoConfig: new HexoConfig(),
    themeConfig: new Theme()
};

// getters
const getters = {
    shouldPagination: () => {
        return state.hexoConfig.per_page !== 0;
    }
};

// actions
const actions = {
    fetchMeta: async ({ commit }) => {
        const { data } = await fetchHexoConfig();
        commit('reloadMeta', data);
    },
    loadGoogleAnalytics: ({ state }, { router }) => {
        const google_analytics = state.themeConfig.google_analytics;
        if (google_analytics.enable) {
            const track_id = google_analytics.track_id;
            Vue.use(installGoogleAnalytics, { router, track_id });
        }
    }
};

// mutations
const mutations = {
    reloadMeta: (state, data) => {
        state.hexoConfig = new HexoConfig(data);
        state.themeConfig = new Theme(data);
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}


